import {
    getTabInfo,
    Tab,
    isEasyListEnabled,
    removeBrowsingTopics
} from "@/content-scripts/content-ads/utils";
import { isAdsProtectionActive } from "@/content-scripts/common-utils";
import { useLogging } from "@/content-scripts/heuristics-db/content-heuristics";
import { injectCSS } from "@/utils/utils.js";
import { RESOURCE_CONTENT_STYLE } from "@/app/scripts/app-consts.js";
import {
    downloadEasylistSelectors,
    serializeEasylist,
    checkDomElementsForAds,
    DomObserver,
    facebookSpecificRemovals,
    twitterSpecificRemovals,
    youtubeSpecificRemovals,
    redditSpecificRemovals,
    linkedinSpecificRemovals,
    instartSpecificRemovals
} from "@/content-scripts/content-ads/utils";
import type { EasyListElement } from "@/content-scripts/types/easyListElements";
import { ProcessedRules } from "@/content-scripts/heuristics-db/types";
import { IsAdProtectionActiveResponse } from "@/utils/messaging/types";

let globalEasylist: EasyListElement;
let heuristicCacheObj = {
    db: {} as ProcessedRules | {}
};


const allAdsBlocking = async () => {
    const currentTab: Tab = await getTabInfo();

    if (!currentTab || !currentTab.id || !currentTab.url) return;

    const protectionStatus: IsAdProtectionActiveResponse = await isAdsProtectionActive(currentTab?.id, currentTab?.url);

    // Skip ALL if Ads Protection is disabled
    if (!protectionStatus.isAdsProtectionActive) { return; }

    // Conditioned only by ads protection
    removeBrowsingTopics();

    // Inject styles for classes used to hide ads
    injectCSS(RESOURCE_CONTENT_STYLE);


    //Get EasyList unprocessed selectors
    if (protectionStatus.isEasylistProtectionActive) {
        const rawEasylist = await downloadEasylistSelectors(currentTab);
        globalEasylist = serializeEasylist(rawEasylist, currentTab.id);
    }


    // Check the DOM elements for ads and remove them
    checkDomElementsForAds(currentTab, globalEasylist, heuristicCacheObj, protectionStatus);


    // Start DOM observation for dynamically added elements
    const domObserver = DomObserver(currentTab, globalEasylist, heuristicCacheObj, protectionStatus);
    domObserver.start();

    // Optional: Clean up on page unload
    window.addEventListener('beforeunload', () => {
        domObserver.stop();
    });

}


// Run before DOMContentLoaded
facebookSpecificRemovals();
twitterSpecificRemovals();
youtubeSpecificRemovals();
redditSpecificRemovals();
linkedinSpecificRemovals();
instartSpecificRemovals();

document.addEventListener("DOMContentLoaded", function () {

    // run ads blocking
    allAdsBlocking();

});
